import React,{useState} from 'react';
import photo1 from '../../../assets/img/1.jpg';
import photo2 from '../../../assets/img/2.jpg';
import photo3 from '../../../assets/img/3.jpg';
import photo4 from '../../../assets/img/4.jpg';
import photo5 from '../../../assets/img/5.jpg';
import {Link,Redirect} from 'react-router-dom';
import classes from '../card/card.css';
import Basket from '../basket/basket';
import {connect} from 'react-redux';
import Button from '../../UI/Button/Button';
import Layout from '../../../continar/Layout/Layout';
import * as  action from '../../../store/action/index';
import Spinner from '../../UI/spinner/Spinner';
const Card =(props)=>{
    

  let flowerord=<Spinner/>
   if(props.flowers){
       flowerord=props.flowers.map((mp,index)=>(
        mp.some > 0?<div key={mp.id} className={classes.card}>
<Link className={classes.link} to={`/${mp.name}/places`}>
                    <div className={classes.divimg}><img src={`data:image/jpg;base64,${mp.photo}`}  className={classes.card_img}/></div>
                    <h2 className={classes.card_name}>{mp.name}</h2>
                    <p className={classes.card_text}>{mp.imageCover.slice(0,30)}...</p>
                    </Link>
        <Basket click={index} ctd={mp.some}></Basket>
    </div>:null
    ))
   }
   const formhandler=()=>{
     props.history.push('/form')
   }
   
   
   const someflower= props.flowers.filter(e=> e.some > 0);
   
   
   let order = <Link to="/"/>
   if(someflower.length > 0){
  
    order= (
        <div className={classes.ordershop}>
            <Layout/>
            <div className={classes.Button}><Button click={formhandler}> ثبت</Button></div>
        <div className={classes.section}>
            
           {flowerord}
    
        </div>
        </div>
    );
   }
  return order;
   
    
};
const mapstatetoprops=state=>{
    return{
    card: state.card.person,
    bask: state.card.totalsome,
    flowers:state.get.flowers
}}
const dispatchtoprops=state=>{
    return{
        onadd: (id)=>state(action.setorder(id))
    }
}
export default connect(mapstatetoprops,dispatchtoprops)(Card);